import React from "react";
import MainButton from "../MainButton";
import MainButton2 from "../MainButton2";
import MainButton3 from "../MainButton3";
import MainButton4 from "../MainButton4";
import MainButton5 from "../MainButton5";
import "./StartMenu.css";
import { useHistory } from "react-router-dom"

function StartMenu(props) {
  const { mainButtonProps, mainButton2Props, mainButton3Props, mainButton4Props, mainButton5Props } = props;
  let history = useHistory();

  return (
    <div className="start-menu">
      <MainButton onClick={()=> history.push("/WelcomePrompt")}>{mainButtonProps.children}</MainButton>
      <MainButton2>{mainButton2Props.children}</MainButton2>
      <MainButton3>{mainButton3Props.children}</MainButton3>
      <MainButton4>{mainButton4Props.children}</MainButton4>
      <MainButton5>{mainButton5Props.children}</MainButton5>
    </div>
  );
}

export default StartMenu;
